<template>
  <div style="padding-top: 100px;">
    <v-card class="mx-auto" max-width="450" height="350">
      <v-tabs>
        <v-tab>修改密码</v-tab>
        <v-tab>修改邮箱</v-tab>

        <v-tab-item>
          <v-container class="mt-4">
            <v-form>
              <v-row
                v-for="item in resetPwdData"
                :key="item.name"
                align="center"
                justify="center"
                no-gutters
              >
                <v-col cols="3" style="text-align: end;">
                  <p>{{ item.name }}</p>
                </v-col>
                <v-col cols="6">
                  <v-text-field
                    v-model="item.model"
                    dense
                  />
                </v-col>
              </v-row>
              <v-row
                align="center"
                justify="center"
              >
                <v-col style="text-align: center;" class="mt-4">
                  <v-btn
                    depressed
                    dark
                    color="primary"
                    width="100"
                    :loading="resetPwdloading"
                    @click="resetUserPwd"
                  >确认修改</v-btn>
                </v-col>
              </v-row>
            </v-form>
          </v-container>
        </v-tab-item>

        <v-tab-item>
          <v-container  class="mt-4">
            <v-form>
              <v-row
                v-for="(item, index) in resetEmailData"
                :key="item.name"
                align="center"
                justify="center"
                no-gutters
              >
                <v-col cols="3" style="text-align: end;">
                  <p>{{ item.name }}</p>
                </v-col>
                <v-col :cols="index === 2 ? 5 : 6">
                  <v-text-field
                    v-model="item.model"
                    dense
                  />
                </v-col>
                <v-btn
                  v-show="index === 2"
                  class="mt-n3"
                  icon
                >
                  <v-icon color="primary" @click="getEmailVerifyCode">mdi-email-send-outline</v-icon>
                </v-btn>
              </v-row>
              <v-row
                align="center"
                justify="center"
              >
                <v-col style="text-align: center;" class="mt-4">
                  <v-btn
                    depressed
                    dark
                    color="primary"
                    width="100"
                    @click="resetUserEmail"
                    :loading="resetEmailLoading"
                  >确认修改</v-btn>
                </v-col>
              </v-row>
            </v-form>
          </v-container>
        </v-tab-item>
      </v-tabs>
    </v-card>
  </div>
</template>

<script>
import { removeToken } from '@/lib/storage/token'
export default {
  data: () => ({
    resetPwdloading: false,
    resetEmailLoading: false,
    resetPwdParam: {
      oldPassword: '',
      password: ''
    },
    resetEmailParam: {
      code: '',
      email: '',
      password: ''
    },
    resetPwdData: [
      { name: '旧密码：', model: '' },
      { name: '新密码：', model: '' },                                                                                                                                                                                
      { name: '确认密码：', model: '' }
    ],
    resetEmailData: [
        { name: '新邮箱：', model: '' },
        { name: '账号密码：', model: '' },                                                                                                                                                                                
        { name: '验证码：', model: '' }
      ]
  }),

  computed: {
  },

  methods: {
    resetUserPwd() {
      this.resetPwdloading = true
      this.resetPwdParam.oldPassword = this.resetPwdData[0].model
      this.resetPwdParam.password = this.resetPwdData[1].model
      this.$store.dispatch('resetUserPwd', this.resetPwdParam).then(() => {
        removeToken()
        this.$router.push('/login')
      }).catch(()=> {
        this.resetPwdloading = false
      })
    },
    getEmailVerifyCode() {
      this.$store.dispatch('getEmailVerifyCode', this.resetEmailData[0].model)
    },
    resetUserEmail() {
      this.resetEmailLoading = true
      this.resetEmailParam.code = this.resetEmailData[2].model.toLowerCase()
      this.resetEmailParam.email = this.resetEmailData[0].model
      this.resetEmailParam.password = this.resetEmailData[1].model
      this.$store.dispatch('resetUserEmail', this.resetEmailParam).then(() => {
        removeToken()
        this.$router.push('/login')
      }).catch(()=> {
        this.resetEmailLoading = false
      })
    }
  }
}
</script>